.gallery {
    &__img {
        margin-left: auto;
        margin-right: auto;
        width: 280px;
        height: 130px;
        border-radius: 8px;
        transform: translateY(-32px);

        @include dq(only-tablet) {
            width: 705px;
            height: 327px;
            border-radius: 18px;
            transform: translateY(-144px);
        }

        @include dq(desktop) {
            width: 1013px;
            height: 422px;
            border-radius: 24px;
            transform: translateY(-98px);
        }
    }
}